












import { Component, Model, Prop, Vue } from "vue-property-decorator";

@Component({
  setup(props, { emit }) {
    const onChange = (newVal: boolean) => {
      emit("input", newVal);
    };
    return {
      onChange,
    };
  },
})
export default class ApplicationsControllerState extends Vue {
  @Prop(Boolean) value: boolean;
  @Prop(String) title: string;
}
